// Define the function createEleWithClass to create an HTML element with a specified class
const createEleWithClass = (tag, className) => {
  const ele = document.createElement(tag);
  ele.className = className;
  return ele;
};

// Define the Slides class
class Slides {
  constructor(data) {
    this.data = data;
    this.container = document.createElement("div");
    this.container.className = "slides";
    this.currentIdx = 0;
    this.slides = this.data.map((entry, index) => {
      const slide = document.createElement("div");
      slide.className = "slide";
      const title = document.createElement("h1");
      title.className = "slide-title";
      const meta = document.createElement("p");
      meta.className = "slide-meta";
      const more = document.createElement("a");
      more.className = "slide-more";
      
      more.addEventListener("touchstart", function(event) {
        event.preventDefault();
        window.open(entry.more, "_self");
      });
      more.href = entry.more;

      slide.classList.add(index !== 0 ? "next" : "show-meta");
      meta.innerHTML = entry.meta;
      title.innerHTML = entry.title;
      more.innerHTML = 'Read more';

      // Conditionally append chevron animation to slide
      if (entry.hasChevron) {
        const chevronContainer = document.createElement("div");
        chevronContainer.className = "container fade-in";
        const chevron1 = document.createElement("div");
        chevron1.className = "chevron";
        const chevron2 = document.createElement("div");
        chevron2.className = "chevron";
        const chevron3 = document.createElement("div");
        chevron3.className = "chevron";
        const textdown = document.createElement("span");
        textdown.className = "textdown";
        textdown.textContent = "Swipe Down";

        chevronContainer.appendChild(chevron1);
        chevronContainer.appendChild(chevron2);
        chevronContainer.appendChild(chevron3);
        chevronContainer.appendChild(textdown);
        
        slide.appendChild(chevronContainer);
      }
      
      // Conditionally append thevron animation to slide
      if (entry.hasThevron) {
        const thevronContainer = document.createElement("div");
        thevronContainer.className = "containers fade-in";
        if (index === 0) {
          thevronContainer.classList.add("first-slide-visible");
        }
        const thevron1 = document.createElement("div");
        thevron1.className = "thevron";
        const thevron2 = document.createElement("div");
        thevron2.className = "thevron";
        const thevron3 = document.createElement("div");
        thevron3.className = "thevron";
        const text = document.createElement("span");
        text.className = "text";
        text.textContent = "Swipe Up";

        thevronContainer.appendChild(thevron1);
        thevronContainer.appendChild(thevron2);
        thevronContainer.appendChild(thevron3);
        thevronContainer.appendChild(text);
        
        slide.appendChild(thevronContainer);
      }

      // Add new div with 1 span showing progress
      const progressContainer = document.createElement("div");
      progressContainer.className = `progress-container fade-in ${index === 0 ? 'first-slide-visible' : ''}`;

      const numbersContainer = document.createElement("div");
      numbersContainer.className = "numbers-container";
      
      const firstNumSpan = document.createElement("span");
      firstNumSpan.className = "first-num";
      firstNumSpan.textContent = `${index + 1}`;
      const slashSpan = document.createElement("span");
      slashSpan.className = "slash";
      slashSpan.textContent = "/";
      const lastNumSpan = document.createElement("span");
      lastNumSpan.className = "last-num";
      lastNumSpan.textContent = "5";

      const clickDragSpan = document.createElement("span");
      clickDragSpan.className = "click-drag";
      clickDragSpan.innerHTML = "click & drag";

      numbersContainer.appendChild(firstNumSpan);
      numbersContainer.appendChild(slashSpan);
      numbersContainer.appendChild(lastNumSpan);

      progressContainer.appendChild(numbersContainer);
      progressContainer.appendChild(clickDragSpan);

      slide.appendChild(progressContainer);

      slide.appendChild(meta);
      slide.appendChild(title);
      slide.appendChild(more);

      this.container.appendChild(slide);
      return slide;
    });
  }

  mount(container) {
    container.appendChild(this.container);
  }

  onActiveIndexChange(activeIndex) {
    this.currentIdx = activeIndex;
    for (let i = 0; i < this.slides.length; i++) {
      if (activeIndex === i) {
        this.slides[i].classList.remove("next");
        this.slides[i].classList.remove("prev");
        // Add visible class to trigger fade-in
        const chevronContainer = this.slides[i].querySelector('.container');
        const thevronContainer = this.slides[i].querySelector('.containers');
        const progressContainer = this.slides[i].querySelector('.progress-container');
        if (chevronContainer) chevronContainer.classList.add("visible");
        if (thevronContainer) thevronContainer.classList.add("visible");
        if (progressContainer) progressContainer.classList.add("visible");
      } else {
        if (activeIndex > i) {
          this.slides[i].classList.remove("next");
          this.slides[i].classList.add("prev");
        } else {
          this.slides[i].classList.add("next");
          this.slides[i].classList.remove("prev");
        }
        // Remove visible class when not active
        const chevronContainer = this.slides[i].querySelector('.container');
        const thevronContainer = this.slides[i].querySelector('.containers');
        const progressContainer = this.slides[i].querySelector('.progress-container');
        if (chevronContainer) chevronContainer.classList.remove("visible");
        if (thevronContainer) thevronContainer.classList.remove("visible");
        if (progressContainer) progressContainer.classList.remove("visible");
      }
    }
  }

  onMove(indexFloat) {
    this.container.style.transform = `translateY(${(indexFloat * 100) / this.slides.length}%)`;
  }

  appear() {
    this.container.classList.add("scrolling");
    this.slides[this.currentIdx].classList.remove("show-meta");
  }

  disperse(activeIndex) {
    this.slides[this.currentIdx].classList.add("show-meta");
    this.container.classList.remove("scrolling");
    for (let index = 0; index < this.data.length; index++) {
      if (index > activeIndex) {
        this.slides[index].classList.add("next");
        this.slides[index].classList.remove("prev");
      } else if (index < activeIndex) {
        this.slides[index].classList.remove("next");
        this.slides[index].classList.add("prev");
      } else {
        this.slides[index].classList.remove("next");
        this.slides[index].classList.remove("prev");
      }
    }
  }
}

export { Slides };
